import { useState, useEffect, memo } from "react";
import NosotrosServie from "../../services/NosotrosServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormNosotros from "./FormNosotros";
import useMateriales from "../../hooks/useMateriales";
import { APP_DEBUG_MODE } from "../../helpers";

const Nosotros = () => {
    const {  currentNosotros,  setCurrentNosotros } =
    useMateriales();



    const updateNosotros = (data) => {
        APP_DEBUG_MODE   &&     console.log(data);
        NosotrosServie.update(data)
            .then((response) => {

                setCurrentNosotros({
                    name: response.data.name || "",
                    descripcion: response.data.descripcion || "",
                    url: response.data.url || "",
                    domicilio: response.data.domicilio || "",
                    estado: response.data.estado || "",
                    municipio: response.data.municipio || "",
                    phone: response.data.phone || "",
                    whatsapp: response.data.whatsapp || "",
                    face: response.data.face || "",
                    correo: response.data.correo || "",
                });
                Swal.fire({
                    title: "Actualizado",
                    text: "Datos actualizados con éxito",
                    icon: "success",
                    confirmButtonText: "OK",
                });

            })
            .catch((err) => {
                alert("Ocurrio un error actualizando");
                APP_DEBUG_MODE &&     console.log(err);
            });
    };

    return (
        <div className="submit-form">
            {!currentNosotros ? (
                <div>
                    <h4>Por favor selecciona un registro</h4>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            name: "",
                            domicilio: "",
                            url: "",
                            estado: "",
                            municipio: "",
                            phone: "",
                            descripcion: "",
                            face: "",
                            whatsapp: "",
                            correo: "",
                        }}
                        onSubmit={(values) => {
                            APP_DEBUG_MODE   &&         console.log(values);
                            updateNosotros(values);
                        }}
                        validationSchema={Yup.object({
                            name: Yup.string().required("Requerido"),
                            url: Yup.string().required("Requerido"),
                            domicilio: Yup.string().required("Requerido"),
                            phone: Yup.string().required("Requerido"),
                            municipio: Yup.string().required("Requerido"),
                            estado: Yup.string().required("Requerido"),
                            descripcion: Yup.string().required("Requerido"),
                            whatsapp:  Yup.string().required("Requerido"),
                            face:  Yup.string().required("Requerido"),
                            correo:  Yup.string()
                            .email('El correo no tiene un formato válido')
                            .required("Requerido"),

                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="row g-3">
                                    <FormNosotros currentNosotros={currentNosotros} />
                                </div>
                                <div className="col-12">

                                    <button type="submit" className="float-end btn btn-primary">Actualizar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(Nosotros);
