import { useEffect } from "react";
import { useFormikContext } from "formik";
import { MyTextInput } from "../formik";

export default function FormUser({ currentUser }) {
    const formikProps = useFormikContext();

    const iniciar = (currentUser) => {
        formikProps.setFieldValue("id", currentUser.id);

        formikProps.setFieldValue("name", currentUser.name);

        formikProps.setFieldValue("username", currentUser.username);
        formikProps.setFieldValue("password", currentUser.password);

    };
    useEffect(() => {
        iniciar(currentUser);
    }, [currentUser]);

    return (
        <>
           <div className="form-group mb-3 ">
             <MyTextInput
              label="Nombre"
              name="name"
              id="name"
               type="text"
             placeholder="Nombre"
                        />
             </div>



            <div className="form-group mb-3">
                <MyTextInput
                   label="Usuario"
                     name="username"
                     id="username"
                     type="text"
                     placeholder="Usuario"
                      />




                     </div>

            <div className="form-group mb-3">
            <MyTextInput
                   label="Password"
                     name="password"
                     id="password"
                     type="password"
                     placeholder="Password"
                      />


            </div>


        </>
    );
}
