import { startUploadingVideos } from "../../actions/notes";
import Swal from "sweetalert2";
import { useFormikContext, Field, ErrorMessage } from "formik";
import { memo } from "react";
import { APP_DEBUG_MODE } from "../../helpers";

const FileAdd = () => {
    const formikProps = useFormikContext();

    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            Swal.fire({
                title: "Cargando..",
                text: "Por favor no cierre hasta que termine de cargar...",
                allowOutsideClick: false,
                onBeforeOpen: () => {
                    Swal.showLoading();
                },
            });

            APP_DEBUG_MODE   &&      console.log(file);
            const result = startUploadingVideos(file).then(
                function (value) {

                    formikProps.setFieldValue(
                        "url",
                       value
                    );

                    Swal.close();
                    Swal.fire({
                        title: ":D",
                        allowOutsideClick: false,
                        text: "Su video fue guardado correctamente",
                        html:
                            '<iframe width="560" height="315" src="' +
                            value +
                            '" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>',
                        imageWidth: 400,
                        imageHeight: 200,
                        imageAlt: "Custom image",
                    })
                },
                function (reason) {
                    APP_DEBUG_MODE &&     console.log(reason);
                }
            );
        }
    };

    const handlePictureClick = () => {
        document.querySelector("#fileSelector").click();
    };
    return (
        <>

            <div className="mb-3">
                <label htmlFor="formFile" className="form-label">Imagen</label>
                <input className="form-control"
                  id="fileSelector"
                 type="file"
                 name="file"
                 accept="video/*"
                onChange={handleFileChange}/>
            </div>

            <Field name="url" id="url" type="hidden" />
            <ErrorMessage name="url" component="span" />
        </>
    );
};

export default memo(FileAdd);
