import { useState,  memo } from "react";
import UserServie from "../../services/UserServie";
import { default as User } from "./User";
import { Modal } from "react-bootstrap";
import { DataGrid } from "@mui/x-data-grid";
import moment from "moment";
import Swal from "sweetalert2";
import { Header } from "../../views/common";
import useMateriales from "../../hooks/useMateriales";
import { APP_DEBUG_MODE } from "../../helpers";

const UserList = () => {
    const columns = [
        { field: "id", headerName: "#", width: 70 },
        { field: "name", headerName: "Nombre", width: 300 },
        { field: "username", headerName: "Usuario", width: 300 },
        {
            field: "created_at",
            headerName: "Fecha de creacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.created_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },
        {
            field: "updated_at",
            headerName: "Ultima actualizacion",
            width: 200,
            renderCell: (params) => (
                <p>
                    {" "}
                    {moment(`${params.row.updated_at}`)
                        .subtract(10, "days")
                        .calendar()}{" "}
                </p>
            ),
        },

        {
            field: "Acciones",
            headerName: "Acciones",
            width: 130,

            renderCell: (params) => (
                <>
                    <i
                        style={{color: "green" }}
                        className="fa-solid fa-pen-to-square"
                        onClick={() => setActiveModal(params.row.id)}
                    ></i>
                    <i
                         className="fa-solid fa-trash-can" style={{color: "red" }}
                        onClick={() => deleteUser(params.row.id)}
                    ></i>
                </>
            ),
        },
    ];

    const {  user, setUser } =
    useMateriales();

    const [currentUser, setCurrentUser] = useState(null);
    const [lgShow, setLgShow] = useState(false);
    const retriveUsers = () => {
        UserServie.getAll()
            .then((response) => {
                setUser(response.data);
                APP_DEBUG_MODE   &&       console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE &&   console.log(err);
            });
    };

    const setActiveModal = (id) => {
        UserServie.getById(id)
            .then((response) => {
                setCurrentUser(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE &&    console.log(err);
            });

        setLgShow(true);
    };

    const setHideModal = () => {
        retriveUsers();
        setLgShow(false);

    };

    const deleteUser = (id) => {


        Swal.fire({
            title: "¿ Estás seguro ?",
            text: "No podrás revertir los cambios",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            cancelButtonText: "Cancelar",
            confirmButtonText: "Sí, eliminarlo",
          }).then((result) => {
            if (result.isConfirmed) {
                Swal.fire("¡Eliminado!", "El usuario ha sido eliminado correctamente.", "success")


              UserServie.remove(id)
              .then((response) => {
                  retriveUsers()
              })
              .catch((err) => {
                  alert("Ocurrio un error al tratar de eliminar el Usuario")
                  APP_DEBUG_MODE &&    console.log(err)
              })

            }
          })





    };

    return (
        <>

        <Header title="Usuarios" main>

</Header>
            <Modal
                size="lg"
                show={lgShow}
                onHide={() => setHideModal()}
                aria-labelledby="example-modal-sizes-title-lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        Editar Usuario
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <User id={currentUser ? currentUser.id : 0} />
                </Modal.Body>
            </Modal>
            <div style={{ height: 500, width: "100%" }}>
                <DataGrid
                    rows={user}
                    columns={columns}
                    pageSize={10}
                    rowsPerPageOptions={[5]}
                />
            </div>
        </>
    );
};

export default memo(UserList);
