import { memo, useEffect } from "react";
import { useFormikContext } from "formik";
import { MyTextInput } from "../formik";
import FileAdd from "./FileAdd";
import MaterialCard from "../materiales/MaterialCard";
import MaterialImage from "../materiales/MaterialImage";
import { Header } from "../../views/common";

function FormNosotros({ currentNosotros }) {
    const formikProps = useFormikContext();

    const iniciar = (currentNosotros) => {
        formikProps.setFieldValue("url", currentNosotros.url);

        formikProps.setFieldValue("name", currentNosotros.name);

        formikProps.setFieldValue("descripcion", currentNosotros.descripcion);
        formikProps.setFieldValue("estado", currentNosotros.estado);
        formikProps.setFieldValue("municipio", currentNosotros.municipio);
        formikProps.setFieldValue("phone", currentNosotros.phone);

        formikProps.setFieldValue("domicilio", currentNosotros.domicilio);
        formikProps.setFieldValue("face", currentNosotros.face);
        formikProps.setFieldValue("whatsapp", currentNosotros.whatsapp);
        formikProps.setFieldValue("correo", currentNosotros.correo);

    };
    useEffect(() => {
        iniciar(currentNosotros);
    }, [currentNosotros]);

    return (
        <>
            <Header title="Nosotros" main></Header>

            <div className="row rows-cols-1 row-cols-md-3 g-3 animate__animated animate__fadeIn notes__image">
                <div className="col-md-6">
                <MyTextInput
                    label="Nombre de empresa"
                    name="name"
                    id="name"
                    type="text"
                    placeholder="Nombre de empresa"
                />

                <FileAdd />

                <MyTextInput
                    label="Phone"
                    name="phone"
                    id="phone"
                    type="text"
                    placeholder="Phone"
                />

                <MyTextInput
                    label="whatsapp"
                    name="whatsapp"
                    id="whatsapp"
                    type="text"
                    placeholder="whatsapp"
                />
             </div>



                {" "}
                {currentNosotros.url != "" ? (
                    <MaterialCard
                        key={currentNosotros.id}
                        material={currentNosotros}
                    >
                        {() => (
                            <>
                                <MaterialImage
                                    className="custom-image"
                                    style={{
                                        boxShadow:
                                            "10px 10px 10px rgba(0,0,0,0.2)",
                                    }}
                                />
                            </>
                        )}
                    </MaterialCard>
                ) : (
                    <></>
                )}{" "}
            </div>

            <div className="col-12">
                <MyTextInput
                    label="Descripcion"
                    name="descripcion"
                    id="descripcion"
                    type="text"
                    placeholder="Descripcion"
                />
            </div>
            <div className="col-12">
                <MyTextInput
                    label="Domicilio"
                    name="domicilio"
                    id="domicilio"
                    type="text"
                    placeholder="Domicilio"
                />
            </div>
            <div className="col-md-6">
                <MyTextInput
                    label="Municipio"
                    name="municipio"
                    id="municipio"
                    type="text"
                    placeholder="Municipio"
                />
            </div>
            <div className="col-md-6">
                <MyTextInput
                    label="Estado"
                    name="estado"
                    id="estado"
                    type="text"
                    placeholder="Estado"
                />
            </div>

            <div className="col-md-12">
                <MyTextInput
                    label="face"
                    name="face"
                    id="face"
                    type="text"
                    placeholder="face"
                />
            </div>
            <div className="col-md-12">
                <MyTextInput
                    label="correo"
                    name="correo"
                    id="correo"
                    type="text"
                    placeholder="correo"
                />
            </div>

        </>
    );
}
export default memo(FormNosotros);
