import { useState, useEffect, memo } from "react";
import UserServie from "../../services/UserServie";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "../../styles/validaciones.scss";
import Swal from "sweetalert2";
import FormUser from "./FormUser";
import { APP_DEBUG_MODE } from "../../helpers";

const User = ({ id }) => {
    const initialMovieState = {
        id: null,
        name: "",
        username: "",
        password: "",
    };

    const [currentUser, setCurrentUser] = useState(initialMovieState);

    const getUser = (id) => {
        UserServie.getById(id)
            .then((response) => {
                setCurrentUser({
                    id: response.data.id || null,
                    name: response.data.name || "",
                    username: response.data.username || "",
                    password: response.data.password || "",
                });
            })
            .catch((err) => {
                alert("Ocurrio un error");
                APP_DEBUG_MODE &&  console.log(err);
            });
    };

    useEffect(() => {
        getUser(id);
    }, [id]);

    const updateUser = (data) => {
        UserServie.update(currentUser.id, data)
            .then((response) => {
                Swal.fire({
                    title: "Actualizado",
                    text: "El Usuario fue actualizado con éxito",
                    icon: "success",
                    confirmButtonText: "OK",
                });
                APP_DEBUG_MODE   &&     console.log(response.data);
            })
            .catch((err) => {
                alert("Ocurrio un error actualizando el Usuario");
                APP_DEBUG_MODE &&     console.log(err);
            });
    };

    return (
        <div className="submit-form">
            {!currentUser ? (
                <div>
                    <h4>Por favor selecciona un registro</h4>
                </div>
            ) : (
                <div>
                    <Formik
                        initialValues={{
                            id: null,
                            name: "",
                            username: "",
                            password: "",
                        }}
                        onSubmit={(values) => {
                            APP_DEBUG_MODE   &&        console.log(values);
                            updateUser(values);
                        }}
                        validationSchema={Yup.object({
                            username: Yup.string().required("Requerido"),
                            name: Yup.string().required("Requerido"),
                            password: Yup.string().required("Requerido"),
                        })}
                    >
                        {(formik) => (
                            <Form>

                                <div className="modal-body">
                                    <FormUser currentUser={ currentUser } />
                                </div>
                                <div className="modal-footer">

                                    <button type="submit" className="btn btn-primary">Actualizar</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            )}
        </div>
    );
};

export default memo(User);
